interface ApproveDependentProps {
  isModalOpen: boolean;
  memberName: string;
  remarks: string;
  possiblePrincipals: any[];
  id: string;
  row: number;
  onClose: (row: number) => void;
  onApprove: (row: number, selectedTempId: string) => void;
}

export const ApproveDependentModal: React.FC<ApproveDependentProps> = (
  props: ApproveDependentProps,
): JSX.Element => {
  const { id } = props;
  const [isModalOpen, setIsModalOpen] = useState(props.isModalOpen);
  const [dropdownChoice, setDropdownChoice] = useState({ 
    value: '',
    temp_id: '', 
    first_name: '', 
    middle_name: '',
    last_name: '',
    date_of_birth: '', 
    is_vip: false, 
    civil_status: '', 
    gender: '', 
    batch_name: '',
    isValue: true
  });
  const classes = useStyles();

  useEffect(() => {
    setIsModalOpen(props.isModalOpen);
    if (props.isModalOpen) {
      setDropdownChoice({
        value: '',
        temp_id: '', 
        first_name: '', 
        middle_name: '',
        last_name: '',
        date_of_birth: '', 
        is_vip: false, 
        civil_status: '', 
        gender: '', 
        batch_name: '',
        isValue: true
      });
    }
  }, [props.isModalOpen]);

  const onApprove = () => {
    if (props.onApprove) {
      props.onApprove(props.row, dropdownChoice.temp_id);
    }
  };

  return (
    <Dialog
      id={id}
      maxWidth={false}
      open={isModalOpen}
      scroll={'paper'}
      aria-labelledby="modal-title"
      className={classes.dialog}
      style={{ zIndex: 9999 }}
    >
      <DialogTitle
        className={classes.dialogTitle}
        disableTypography={true}
        id="modal-title"
      >
        <div className={classes.dialogFirstTitle}>Approve Member (Dependent)</div>
        <IconButton
          aria-label="close"
          className={classes.closeButton}
          onClick={() => props.onClose(props.row)}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.dialogContent}>
        <Grid container>
          <Grid item xs={12}>
            <InformationLabel 
              name={"DEPENDENT NAME"}
              value={props.memberName}
            />
            <InformationLabel 
              name={"CONFLICT"}
              value={props.remarks}
            />
            <div style={{paddingTop: '2rem'}} />
            <CustomAutocomplete
              id={'add_details_contract'}
              options={props.possiblePrincipals}
              label={'Choose Principal *'}
              value={dropdownChoice}
              error={false}
              error_message={''}
              formatOptionLabel={({first_name, middle_name, last_name, date_of_birth, is_vip, civil_status, gender, batch_name, isValue}) => (
                <div>
                  <div style={{padding: '2px'}}>
                    {GlobalFunction.toNameFormat(first_name, middle_name, last_name)}
                  </div>
                  {!isValue ? (
                    <>
                    <div style={{fontSize: '11px', padding: '2px'}}>
                      {`${GlobalFunction.toCommonDateString(new Date(date_of_birth))}, ${is_vip ? 'VIP' : ''}`}
                    </div>
                    <div style={{fontSize: '11px', padding: '2px'}}>
                      {`${civil_status}, ${gender}, Batch ${batch_name}`}
                    </div>
                    </> 
                  ) : null}
                </div>
              )}
              onChange={(value: any) => {
                setDropdownChoice({
                  ...value,
                  isValue: true
                });
              }}
            />
            <div style={{paddingTop: '4rem'}} />
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions className={classes.dialogAction}>
        <Grid
          container
          direction="row"
          justify="space-evenly"
          alignItems="stretch"
        >
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <Button
              className={classes.cancelButton}
              variant="contained"
              color="secondary"
              onClick={() => props.onClose(props.row)}
            >
              Cancel
            </Button>
          </Grid>
          <Grid item xs={4} style={{ textAlign: 'center' }}>
            <Button
              className={classes.approveButton}
              variant="contained"
              color="primary"
              disabled={dropdownChoice.value === ''}
              onClick={onApprove}
            >
              Approve
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  );
};

ApproveDependentModal.defaultProps = {
  isModalOpen: false,
  id: 'modal-id',
};
